.wrapper-modal {
    display: flex;
    justify-content: center;
}

.modal {
    position: absolute;
    top: 25%;
    width: 90%;
    border-radius: 15px;
    border: 1px solid black;
    background-color: white;
    display: flex;
    flex-wrap: wrap;
}

.employe {
    width: 20%;
    margin: 20px auto;
    background-color: #F3F6FF;
    border-radius: 15px;
    padding: 10px 7px 24px 7px;
}

.employe p {
    text-align: center;
    color: #535353;
    margin: 10px 0px;
}

.employe img {
    display: block;
    width: 40%;
    height: 40%;
    object-fit: contain;
    object-position: center;
    margin: 0px auto;
}

.employe button {
    display: block;
    margin: 0px auto;
    background-color: #3757D5;
    color: white;
    border: none;
    outline: none;
    border-radius: 5px;
    width: 90%;
    height: 30px;
    margin-bottom: 10px;
    cursor: pointer;
    font-weight: bold;
}

.employe button.decline {
    background-color: #AE1A21;
}

@media screen and (max-width: 900px) {
    .employe {
        width: 45%;
        margin: 20px 1%;
    }
}

@media screen and (max-width: 500px) {
    .employe {
        width: 41%;
        margin: 20px 2%;
        height: 100%;
    }
}